

























import Vue from "vue";
import ApplicationUsers from "./applicationUsers.vue";

export default Vue.extend({
  props: {
    application: Object,
  },
  components: {
    ApplicationUsers,
  },
  data: () => ({
    users: {},
    panelModel: null,
    panels: {},
  }),
  mounted() {
    this.setPanels();
  },
  methods: {
    setPanels() {
      this.panels = [
        {
          id: 0,
          title: "Users",
          component: ApplicationUsers,
          props: {
            appId: this.application.id,
            apiKey: this.application.apiKey,
          },
        },
      ];
    },
  },
});
